<template>
  <!-- 漫画详情页 -->
  <div class="detail">
    <!-- 详情页头部 -->
    <detailTop :title="aniData.name" :isShowRight="false"/>
    <!-- 详情页中间图片和漫画的一些信息 -->
    <div class="ani-introduce">
      <div class="ani-img"><img :src="aniData.url" alt=""></div>
      <div class="ani-introduce-message">
        <div>
          <!-- 右侧信息部分 -->
          <div class="author">
            <svg class="icont" aria-hidden="true">
            <use xlink:href="#icon-shenfenrenzheng"></use>
            </svg>
            <span>作者</span></div> 
          <div class="type">
            <svg class="icont" aria-hidden="true">
            <use xlink:href="#icon-bangzhupingjia"></use>
            </svg> 
            <span>冒险 悬疑</span> 
          </div>
          <div class="renqi">
            <svg class="icont" aria-hidden="true">
            <use xlink:href="#icon-shujutongji"></use>
            </svg> 
            <span>人气 208021</span>
          </div>
          <div class="dingyue">
            <svg class="icont" aria-hidden="true">
            <use xlink:href="#icon-mendiandingdan"></use>
            </svg> 
            <span>订阅 921</span> 
          </div>
          <div class="time"> 
            <svg class="icont" aria-hidden="true">
            <use xlink:href="#icon-pingjia"></use>
            </svg>
            <span>2023-10-09 连载中</span>
          </div>
        <div class="button">
          <van-button color="#65c294" size="small" style="width: 80px; height: 30px; margin: 10px 10px 0 0; font-size: 16px; padding-left: 0;" @click="handleClickDY(aniData.name)">{{flag?'取消订阅':'订阅漫画'}}</van-button>
          <van-button plain type="primary" border-color="#ffffff" color="#fff" size="small" style="width: 80px; height: 30px; font-size: 16px;  margin: 10px 10px 0 0; background-color: transparent; font-size: 16px; padding-left: 0;" @click="ClickRead">开始阅读</van-button>
        </div>
        </div>
      </div>
    </div>
    
    <!-- 多行文本介绍剧情 -->
    <van-collapse v-model="activeNames">
      <van-collapse-item title="剧情介绍" name="1">陈情令》的故事背景设定在十六年前，天下五分，分别为姑苏蓝氏、云梦江氏、清河聂氏、岐山温氏和兰陵金氏。在这个时代，温氏一家独大，对其他四家进行压迫和剥削。

江氏故人之子魏无羡和姑苏蓝氏含光君蓝忘机在云梦江氏长大，两人性格迥异，魏无羡性格开朗活泼，蓝忘机则以雅正闻名。他们两人在一次偶然的机会中发现了蓝氏一直以来守护的秘密，决定继承遗志，为苍生消除隐患。

然而，真正的幕后黑手是温氏家主温若寒。在发现这个真相后，众家族联合起来，不堪温氏之辱，合力讨伐温氏。温氏最终被摧毁，但魏无羡为了保护温氏无辜的人，不惜与众家族对立，最终被奸人陷害，酿成大祸，误害了师姐江厌离，自己也不知所踪。

十六年后，消失已久的魏无羡在莫家庄出现，偶遇故人蓝忘机。莫家庄发生了一连串诡异的凶案，使得整个村庄显得扑朔迷离。蓝忘机和魏无羡一起调查此案，逐渐揭开了真相的面纱，发现这一切都是金光瑶所为。

最终，金光瑶身死，蓝忘机承担起了匡扶天下的使命，而魏无羡不忘初心，继续潇洒世间。

整个故事充满了悬疑、惊悚、情感等元素，描绘了一个充满纷争和磨难的时代，同时也展示了友情、亲情和爱情等多种情感。</van-collapse-item>
    </van-collapse>

    <!-- 分享按钮 -->
    <div class="share" style="text-align: center; border-top: 1px solid #ccc;">
      <van-cell title="分享" @click="showShare = true" style="color: #696868;"/>
      <van-share-sheet
        v-model="showShare"
        title="立即分享给好友"
        :options="options"
      />
    </div>

    <!-- 漫画章节 -->
    <div>
      <chapter/>
    </div>

    <!-- 评论区 -->
    <div class="review" style=" background-color: #fff;">
        <p>作品讨论</p>
        <van-divider :style="{color: '#ccc', borderColor: '#ccc'}"/>
        <div>
          <userReview/>
        </div>
    </div>
    
    <van-image-preview v-model="showImg" :images="page_url" :loop="false">
    </van-image-preview>

  </div>

</template>

<script>
import detailTop from './components/detail_top';
import chapter from './components/chapter'
import userReview from './components/userReview'
import store from '../../store'
import axios from 'axios'
import { Toast } from 'vant';
export default {

  components:{
    detailTop,
    chapter,
    userReview
  },
  data(){
    return{
      showImg:false,
      activeNames: ['0'],
      show:false,
      showShare: false,
      flag:false,//漫画是否订阅
      store,
      text:'订阅漫画',
      // flag:false,
      options: [
        [
          { name: '微信', icon: 'wechat' },
          { name: '朋友圈', icon: 'wechat-moments' },
          { name: '微博', icon: 'weibo' },
          { name: 'QQ', icon: 'qq' },
        ],
        [
          { name: '复制链接', icon: 'link' },
          { name: '分享海报', icon: 'poster' },
          { name: '二维码', icon: 'qrcode' },
          { name: '小程序码', icon: 'weapp-qrcode' },
        ],
      ],
      aniData:{},
      page_url:[],
      comic_id: '',
      chapter_id:''
    }
  },
  methods: {
    HandleShare(){
      this.show = true
    },
    onSelect() {
      this.showShare = false;
    },

    // 处理漫画是否被订阅
    handleClickDY(data){
      // 
      this.flag =! this.flag
      const value = {flag:this.flag,data:data}
      this.store.commit('handleClickDY',value)
      this.store.commit('addMyHobby')
    },

    // 点击开始阅读
    ClickRead(){
      const p = new Promise((resolve,reject)=>{
          // 获取漫画资源
          axios.get(`https://www.idmzj.com/api/v1/comic1/chapter/detail?channel=pc&app_name=dmzj&version=1.0.0&timestamp=1695349563013&uid&comic_id=${this.comic_id}&chapter_id=${this.chapter_id}`).then(
            res =>{
            if(res.status === 200){
              resolve(res)
            }
            return
            },
            err =>{
              reject(err)
          })
      })

      p.then(res=>{
        this.page_url = res.data.data.chapterInfo.page_url
        console.log(this.page_url);
        if(this.page_url.length === 0){
          Toast('该漫画没有资源')
          return 
        }
        this.showImg = true
        // ImagePreview(this.page_url);
      },err=>{
        console.log(err);
        Toast('请求出错啦♪(^∇^*)')
        return
      })
      console.log(this.comic_id);
    }
  },
  mounted() {
    // 获取首页传来的数据
    // this.aniData = this.$route.params.data
    // if(this.$route.params.data !== undefined){
    //   localStorage.setItem('aniData',JSON.stringify(this.$route.params.data))
      this.aniData = JSON.parse(localStorage.getItem('aniData'))
      this.flag = this.aniData.flag
      this.comic_id = this.aniData.comic_id
      this.chapter_id = this.aniData.chapter_id



    // }
  },

}
</script>

