import Vue from "vue";
import Vuex from 'vuex'

Vue.use(Vuex)

const actions = {

}

const mutations = {

    // 新增用户订阅的漫画
    addMyHobby(state){
        const newArr = []
        // 1.筛选数组中 flag值为 true的选项
        let Arr = state.AnimationData.map(item=>{
            return item.data.filter(itemData=>{
                if(itemData.flag){
                        return itemData
                }return
            });
        })
        
        // 2.合并多个数组 并加入到新数组
        Arr.forEach(item=>{
            newArr.push(...item)
        })

        // 3.赋值给myhobby数组
        state.myhobby[0].data = newArr
        
    },


    // 用户点击了订阅漫画按钮
    handleClickDY(state,value){
        state.AnimationData.forEach((item) => {
            item.data.forEach(dataItem =>{
                if(dataItem.name === value.data){
                    dataItem.flag = value.flag
                    // console.log(dataItem,'dataItem');
                }
            })
        });
        // console.log(state.AnimationData,'state.AnimationData');
    },

    // 用户点击管理按钮后
    ClickIsShowGuanLiBox(state,value){
        state.isShowGuanLiBox = value
    },

    // 用户是否全选
    handleCheckAll(state){
        state.CheckItem = state.myhobby[0].data
    }
}

