<template>
  <div class="news_item" @click="ClickNewsItem(newsItem)">
    <div class="left_img"><img :src="newsItem.img_url"></div>
    <div class="right_message">
        <div class="title">{{ newsItem.title }}</div>
        <div class="message">作者：{{ newsItem.author }}</div>
    </div>
  </div>
</template>

<script>
import router from '../../../router'
export default {
props:['newsItem'],
data() {
    return {
        sto:null
    }
},
methods: {
    ClickNewsItem(newsItem){
        localStorage.setItem('newsItem',JSON.stringify(newsItem))
        localStorage.setItem('newsItemContent',JSON.stringify(newsItem.content))
        this.sto = setTimeout(() => {
            router.push('/newsdetail')
        }, 1000);
    }
},
destroyed() {
    clearTimeout(this.sto)
},
}
</script>

<style lang="less" scoped>
.news_item{
    display: flex;
    justify-content: space-evenly;
    align-items: center;
    background-color: #fff;
    margin: 10px 0;
    padding:10px;
    .left_img{
        margin-right: 10px;
        img{
        width: 250px;
        height: 150px;
            border-radius: 10px;
        }
    }
    .right_message{
        font-size: 28px;
        display: flex;
        flex-direction: column;
        justify-content: space-between;
        .title{
            margin-top: 20px;
        }
        .message{
            margin: 20px 0;
            font-size: 25px;
            color: #666;
        }
    }
}
</style>