import VueRouter from 'vue-router'
import Vue from "vue";

import animation from '../views/animation/animation.vue'
import news from '../views/news/news.vue'
import story from '../views/story/story.vue'
import my from '../views/my/my.vue'
import main from '../views/main/main.vue'
import anidetail from '../views/anidetail/detail'
import anitotal from '../views/anitotal/anitotal'
import newsdetail from '../views/newsdetail/newsdetail'
import empty from '../views/empty'
import login from '../views/login'

const originalPush = VueRouter.prototype.push
VueRouter.prototype.push = function push(location, onResolve, onReject) {
if (onResolve || onReject) return originalPush.call(this, location, onResolve, onReject)
return originalPush.call(this, location).catch(err => err)
}

Vue.use(VueRouter)
const routes = [
    {   path:'/',
        component:main,
        name:'main',
        redirect:'/animation',
        children:[
            { path:'animation',component:animation,name:'animation' },
            { path:'news',component:news,name:'news' },
            { path:'story',component:story,name:'story' },
            { path:'my',component:my,name:'my'},
        ]
    },
    {
        path:'/aniDetail',
        name:'anidetail',
        component:anidetail
    },
    {
        path:'/anitotal',
        name:'anitotal',
        component:anitotal
    },
    {
        path:'/newsdetail',
        name:'newsdetail',
        component:newsdetail
    },
    {
        path:'/empty',
        name:'empty',
        component:empty
    },
    {
        path:'/login',
        name:'login',
        component:login
    }
]

export default new VueRouter({
    mode:"hash",
    routes
})