<template>
    <!-- 我的订阅展开 -->
  <div class="anitotal">
    <!-- 顶部标题栏 -->
    <detailTop :title="title" :isShowRight="true"/>
    <empty v-show="isEmpty"/>
    <div class="anitotal_grid" v-show="!isEmpty">
      <!-- 订阅的漫画列表 -->
      <aniCard :aniItem="aniItem" :isShowBlack="isShow"/>
    </div>
  </div>
</template>

<script>
import empty from './components/empty';
import detailTop from '../anidetail/components/detail_top';
import aniCard from '../../components/shouye-aniCard'
import store from '../../store'
export default {
    components:{
      detailTop,
      empty,
      aniCard,
      store
    },
    data(){
        return{
          title:'',
          aniItem:[],
          isEmpty:true,
          isShow:true
        }
    },
    mounted() {
      this.title = JSON.parse(localStorage.getItem('detail_title_top'))
      this.aniItem = store.state.myhobby[0]
      console.log(this.aniItem);
      if(store.state.myhobby[0].data.length){
        this.isEmpty = false
      }
    },
}
</script>

<style lang="less" scoped>
.anitotal{
  background-color: #ffffff;
  padding-top: 10px;
  .anitotal_grid{
    margin-top: 125px;
   
  }
}
</style>