<template>
<!-- 首页的栏目标题 -->
<div id="top" @click="HandleClickMyHobby(title)" :class="clickstyle">
    <div class="left">
        <svg class="icon" aria-hidden="true" style="color: red;">
            <use :xlink:href="itemicon"></use>
        </svg>
        <p>{{ title }}</p>
    </div>
    <div class="right">
        <van-icon name="arrow" color="#009ad6" size="20" />
    </div>
</div>
</template>

<script>
import router from '../router'
export default {
    props:['title','itemicon',],
    data(){
        return{
            clickstyle:''
        }
    },
    methods: {
        HandleClickMyHobby(title){
            if(title === '我的订阅'){
                this.clickstyle = 'clickstyle'
                console.log(title);
                
                setTimeout(() => {
                    router.push('/anitotal')
                    localStorage.setItem('detail_title_top',JSON.stringify(title))
                }, 1000);
            }
        }
    },
}
</script>

<style lang="less" scoped>
#top{
    
        display: flex;
        align-items: center;
        justify-content: space-between;
        .left{
            display: flex;
            align-items: center;
            p{
                font-size: 27px;
                font-weight: 500;
                padding-left: 20px;
            }
            .icon {
                width: 1em; height: 1em;
                vertical-align: -0.15em;
                fill: currentColor;
                overflow: hidden;
            }
        }
    }
    .clickstyle:active{
        background-color: #90d7ec;
        transform: all 1s;
    }
</style>