<template>
  <div class="login">
    <div class="form">
            <div class="username">
                <span>账号：</span>
                <input type="text" ref="uname" v-model.trim="uname">
            </div>
            <div class="password">
                <span>密码：</span>
                <input type="password" ref="psw" v-model.trim="psw">
            </div>
            <p style="color: #d93a49; font-size: 20px;">没有账号？立即去注册→</p>
            <button class="submit" @click="HandleLogin">登录</button>
    </div>
  </div>
</template>

<script>
import cookie from 'js-cookie'
import router from '../router'
export default {
    data() {
        return {
            uname:'',
            psw:''
        }
    },
    methods: {
        HandleLogin(){
            if(this.uname!=='' && this.psw!==''){
                if(this.uname==='admin' && this.psw==='admin'){
                    cookie.set('admin','123456')
                    router.push('/')
                }
            }
        }
    },
}
</script>

<style lang="less" scoped>
.login{
    .form{
        width: 95%;
        background-color: #ffffff;
        padding: 50px 0;
        text-align: center;
        border: 1px solid #009ad6;
        border-radius: 20px;
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        .username,.password{
            text-align: center;
            width: 100%;
            margin: 50px 0;
            padding: 10px 0;
            span{
                font-size: 35px;
                color: #009ad6;
            }
            input{
                width: 460px;
                height: 50px;
                outline: none;
                border: none;
                border-bottom: 2px solid #009ad6;
                padding:0 20px;
                font-size: 30px;
                color: #009ad6;
            }
        }
        .submit{
            width: 200px;
            font-size: 40px;
            background-color: #ffffff;
            color: #009ad6;
        }
    }
}
</style>