<template>
    <!-- 详情页的头部返回键 和标题  和其他选项-->
  <div class="detail_top">
    <van-nav-bar v-show="!isShowGuanLiBox"
        :title="title"
        left-arrow
        :right-text="isShowRight?guanli:xiazai"
        @click-left="onClickLeft"
        @click-right="onClickRight"
        fixed
    />
    <div class="box">
        <div class="guanli" v-show="isShowGuanLiBox">
            <div class="left">已选择0项</div>
            <div class="right">
                <span style="color: red;">删除</span>
                <span style="color: #009ad6;" @click="HandleClickCheckAll">全选</span>
                <span style="color: #009ad6;" @click="ClickSuccess">完成</span>
            </div>
        </div>
    </div>
  </div>
</template>

<script>
import { Toast } from 'vant';
import router from '../../../router'
import store from  '../../../store'
export default {
    props:['title','isShowRight',],
    data(){
        return{
            // isShowRight:false
            guanli:'管理',
            xiazai:'下载',
            isShowGuanLiBox:'',
        }
    },
    methods: {
        onClickLeft(){
            console.log(111);
            router.go(-1)
        },
        // 管理点击右边的管理和下载按钮
        onClickRight(){
            // 用户点击了管理
            if(this.isShowRight){
                if(store.state.myhobby[0].data.length<=0){
                    Toast('没有漫画哦(⊙_⊙)')
                    return
                }
                store.commit('ClickIsShowGuanLiBox',true)
                this.isShowGuanLiBox = store.state.isShowGuanLiBox
            }
        },
        // 用户点击完成按钮
        ClickSuccess(){
            // this.isShowGuanLiBox = false
            store.commit('ClickIsShowGuanLiBox',false)
            this.isShowGuanLiBox = store.state.isShowGuanLiBox
        },
        // 用户点击全选按钮
        HandleClickCheckAll(){
            store.commit('handleCheckAll')
            console.log(store.state.CheckItem);
        }
    },
    mounted() {

    },
}
</script>

<style lang="less">
.detail_top{
    margin-bottom: 95px;
    .van-nav-bar__content{
        border-bottom: 5px solid #009ad6;
    }
    .box{
        position: absolute;
        width: 100%;
        .guanli{
            // height: 100px;
            display: flex;
            align-items: center;
            justify-content: space-between;
            border-bottom: 5px solid #009ad6;
            padding: 22px 20px;
            font-size: 28px;
            
            span{
                // padding: 0 20px;
                margin-left: 20px;
            }
        }
    }

}
</style>