<template>
  <!-- 新闻详情页 -->
  <div>
    <div style="margin-bottom: 45px;">
      <van-nav-bar
        title="新闻正文"
        left-arrow
        @click-left="onClickLeft"
        fixed
      />
    </div>

    <div class="message">
      <div class="title">{{ news.title }}</div>
      <div class="author">作者：{{ news.author }}</div>
      <div class="content">{{ newsContent.text }}</div>
      <div class="img"><img :src="item" v-for="(item,index) in newsContent.img" :key="index"></div>
    </div>
  </div>
</template>

<script>
import router from '../../router'
export default {
  data() {
    return {
      news:{}, //新闻信息
      newsContent:{}
    }
  },
methods: {
  onClickLeft(){
    router.go(-1)
  }
},
mounted() {
  // console.log(JSON.parse(localStorage.getItem('newsItem')));
  this.news = JSON.parse(localStorage.getItem('newsItem'))
  console.log(this.news);
  this.newsContent = JSON.parse(localStorage.getItem('newsItemContent'))
},
}
</script>

<style lang="less" scoped>
.message{
  background-color: #fff;
  padding: 20px;
  .author{
    margin: 20px 0;
    color: #808080;
    font-size: 30px;
  }
  .title{
    color: #ef4136;
    font-size: 40px;
  }
  .content{
    font-size: 35px;
    font-weight: 300;
    margin: 10px 0;
    line-height: 2.5;
    letter-spacing:2px
  }
  .img{
    img{
      width: 100%;
      margin: 20px 0;
    }
  }
}
</style>